import React, { useState, useRef, useEffect } from 'react';
import { Form, Row, Col, Button, Container } from 'react-bootstrap';
import { Table, Select, Drawer } from 'antd';

import { PlusCircleOutlined, CloseOutlined } from '@ant-design/icons';

import gbstyle from '../../assets/gbstyle'

export default function FrmStaff() {
    const [data, setData] = useState([]);
    const [showForm, setShowForm] = useState(false);

    const columns = [
        {
            title: 'Staff Name',
            dataIndex: 'staffname',
            key: 'staffname',
        },
        {
            title: 'User Type',
            dataIndex: 'usertype',
            key: 'usertype',
            align: 'center',
        },
        {
            title: 'Contact Number',
            dataIndex: 'cotactnumber',
            key: 'cotactnumber',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Account Status',
            dataIndex: 'accountstatus',
            key: 'accountstatus',
            align: 'center',
        },
        {
            title: 'User',
            dataIndex: 'user',
            key: 'user',
            align: 'center',
        }
    ];

    useEffect(() => {
        let initdata = [];
        for (let i = 1; i <= 50; i++) {
            initdata.push(
                {
                    staffname: 'เอกกวิน อินทน',
                    usertype: 'Sale',
                    cotactnumber: '02-223-2345 ต่อ ' + i,
                    email: 'test_12345@testmail.com',
                    accountstatus: 'Active',
                    user: '4',
                });
        }
        setData(initdata);
    }, []);

    const btnAddClick = () => {
        setShowForm(true)
    }


    return (
        <div>
            <Form>
                <Form.Group >
                    <Row >
                        <Col style={{ ...gbstyle.formtoppic }}>
                            STAFF MANAGEMENT
                        </Col>
                    </Row>
                    <Row style={{ ...gbstyle.formnextrow2, display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                        <Col md={3}>
                            <Form.Control size="sm" type="text" placeholder="Search: Account / Username" style={{ ...gbstyle.formround }} />
                        </Col>
                        <Col md={3}>
                            <Select
                                showSearch
                                allowClear
                                optionFilterProp="children"
                                filterOption={(input, option) => (
                                    option.props.children.toLowerCase()
                                        .indexOf(input.toLowerCase()) >= 0
                                )}
                                style={{ ...gbstyle.formnextcol, width: '100%' }} className="dropdownround"
                                placeholder="All Types"
                            >
                                <Option value="jack">Jack</Option>
                                <Option value="lucy">Lucy</Option>
                                <Option value="Yiminghe">yiminghe</Option>
                            </Select>
                        </Col>
                        <Col md={3}>
                            <Select
                                showSearch
                                allowClear
                                optionFilterProp="children"
                                filterOption={(input, option) => (
                                    option.props.children.toLowerCase()
                                        .indexOf(input.toLowerCase()) >= 0
                                )}
                                style={{ ...gbstyle.formnextcol, width: '100%' }} className="dropdownround"
                                placeholder="All Status"
                            >
                                <Option value="jack">Jack</Option>
                                <Option value="lucy">Lucy</Option>
                                <Option value="Yiminghe">yiminghe (BKK)</Option>
                            </Select>
                        </Col>
                        <Col style={{ display: 'flex', justifyContent: 'flex-end' }}>
                            <Button
                                size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formcontrol }}
                                onClick={() => btnAddClick()}>
                                <PlusCircleOutlined style={{ color: 'white' }} />
                                {' '}Create{' '}
                            </Button>
                        </Col>
                    </Row>
                    <br />
                    <Row>
                        <Col >
                            <Table
                                className="formtable"
                                size="small"
                                pagination={{ position: ["bottomLeft"], pageSize: 15 }}
                                columns={columns} dataSource={data}
                            />
                        </Col>
                    </Row>
                </Form.Group>
            </Form>

            <Drawer
                className="formpopup"
                title="Menu"
                placement={'right'}
                closable={true}
                onClose={() => setShowForm(false)}
                visible={showForm}
                bodyStyle={{ overflowX: 'hidden' }}
                width={'40vw'}
            >
                <div style={{ display: 'flex', flexDirection: 'column' , minHeight: '100%' }}>
                    <div style={{flex: '1'}}>
                        <Row style={{ ...gbstyle.formpopuptitle, ...gbstyle.formpopupborderbottom }}>
                            <Col style={{ ...gbstyle.formtoppic2 }}>
                                Staff Details
                            </Col>
                            <Col md={1} style={{ display: 'flex', justifyContent: 'flex-end' }}>
                                <Button
                                    size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.buttonxsm }}
                                    onClick={() => setShowForm(false)}>
                                    <CloseOutlined className="nofixtop" />
                                </Button>
                            </Col>
                        </Row>
                        <Row style={{ ...gbstyle.formpopupbody }}>
                            <Form style={{ ...gbstyle.controlfull }}>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>User Type</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Full Name</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Email*</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Contact Number</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                            </Form>
                        </Row>
                    </div>

                    <div style={{ flex: 'none' }}>
                        <Row style={{ ...gbstyle.formpopupfooter, alignItems: 'center', width: '100%' }}>
                            <div class="hr-sect" style={{ ...gbstyle.textweight3 }}>Update History</div>
                            <div>

                                <div style={{ display: 'block', clear: 'both', float: 'left' }}>Last Updated: </div>
                                <div style={{ display: 'block', clear: 'both', float: 'left' }}>By: </div>
                            </div>
                        </Row>
                        <Row style={{ ...gbstyle.formpopupfooter, ...gbstyle.formpopupbordertop, alignItems: 'center', justifyContent: 'space-between', paddingRight: '30px' }}>
                            <div>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>ลบ Account</Button>
                            </div>
                            <div>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>Reset Password</Button>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formnextcol }}>Save</Button>
                            </div>
                        </Row>
                    </div>
                </div>

                {/* <div style={{ position: 'absolute', bottom: 0, width: '100%', overflowX: 'hidden' }}>
                    <Row style={{ ...gbstyle.formpopupfooter, alignItems: 'center', width: '100%' }}>
                        <div class="hr-sect" style={{ ...gbstyle.textweight3 }}>Update History</div>
                        <div>

                            <div style={{ display: 'block', clear: 'both', float: 'left' }}>Last Updated: </div>
                            <div style={{ display: 'block', clear: 'both', float: 'left' }}>By: </div>
                        </div>
                    </Row>
                    <Row style={{ ...gbstyle.formpopupfooter, ...gbstyle.formpopupbordertop, alignItems: 'center', justifyContent: 'space-between', paddingRight: '30px' }}>
                        <div>
                            <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>ลบ Account</Button>
                        </div>
                        <div>
                            <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>Reset Password</Button>
                            <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formnextcol }}>Save</Button>
                        </div>
                    </Row>
                </div> */}
            </Drawer>
        </div>
    )
}


