import React, { useState, useRef, useEffect } from 'react';
import { Form, Row, Col, Button, Container } from 'react-bootstrap';
import { Table, Select, Drawer, Checkbox, Image } from 'antd';

import { PlusCircleOutlined, CloseOutlined, FileImageOutlined } from '@ant-design/icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import gbstyle from '../../assets/gbstyle'

export default function FrmCustomer() {
    const [data, setData] = useState([]);
    const [images, setImages] = useState([]);
    const [showForm, setShowForm] = useState(false);

    const columns = [
        {
            title: 'Comapny',
            dataIndex: 'company',
            key: 'company',
        },
        {
            title: 'Contact Person',
            dataIndex: 'contactperson',
            key: 'contactperson',
        },
        {
            title: 'Contact Number',
            dataIndex: 'cotactnumber',
            key: 'cotactnumber',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Documents',
            dataIndex: 'documents',
            key: 'documents',
            render: documents => (
                <>
                    {documents.map(document => {
                        return (
                            <div className="btn btn-primary btn-sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol, ...gbstyle.formtablebutton }}>
                                {document}
                            </div>
                        );
                    })}
                </>
            ),
        }
    ];

    useEffect(() => {
        let initdata = [];
        let imagesdata = [];
        for (let i = 1; i <= 50; i++) {
            initdata.push(
                {
                    company: 'Company A',
                    contactperson: 'เอกกวิน อินทน',
                    cotactnumber: '02-223-2345 ต่อ ' + i,
                    email: 'test_12345@testmail.com',
                    documents: ["บัตรประชาชน", "หนังสือรับรองบริษัท"],
                });
        }

        for (let i = 1; i <= 3; i++) {
            imagesdata.push(i.toString())
        }

        imagesdata.push('empty')

        setData(initdata)
        setImages(imagesdata)
    }, []);

    const btnAddClick = () => {
        setShowForm(true)
    }


    return (
        <div>
            <Form>
                <Form.Group >
                    <Row >
                        <Col style={{ ...gbstyle.formtoppic }}>
                            CUSTOMER LIST
                        </Col>
                    </Row>
                    <Row style={{ ...gbstyle.formnextrow2, display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                        <Col md={3}>
                            <Form.Control size="sm" type="text" placeholder="Search: Customer Name or email" style={{ ...gbstyle.formround }} />
                        </Col>
                        <Col style={{ display: 'flex', flexDirection: 'row' }}>
                            <Select
                                showSearch
                                allowClear
                                optionFilterProp="children"
                                filterOption={(input, option) => (
                                    option.props.children.toLowerCase()
                                        .indexOf(input.toLowerCase()) >= 0
                                )}
                                style={{ ...gbstyle.formnextcol, width: '60%' }} className="dropdownroundsm"
                                placeholder="All Staffs"
                            >
                                <Option value="jack">Jack</Option>
                                <Option value="lucy">Lucy</Option>
                                <Option value="Yiminghe">yiminghe</Option>
                            </Select>

                            <Checkbox style={{ ...gbstyle.formnextcol }}>ลูกค้าที่ยังไม่มี Staff ดูแล</Checkbox>
                        </Col>
                        <Col md={2} style={{ display: 'flex', justifyContent: 'flex-end' }}>
                            <Button
                                size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formcontrol }}
                                onClick={() => btnAddClick()}>
                                <PlusCircleOutlined style={{ color: 'white' }} />
                                {' '}Create{' '}
                            </Button>
                        </Col>
                    </Row>
                    <br />
                    <Row>
                        <Col >
                            <Table
                                className="formtable"
                                size="small"
                                pagination={{ position: ["bottomLeft"], pageSize: 15 }}
                                columns={columns} dataSource={data}
                            />
                        </Col>
                    </Row>
                </Form.Group>
            </Form>

            <Drawer
                className="formpopup"
                title="Menu"
                placement={'right'}
                closable={true}
                onClose={() => setShowForm(false)}
                visible={showForm}
                bodyStyle={{ overflowX: 'hidden', overflowY: 'auto' }}
                width={'40vw'}
            >
                <div style={{ display: 'flex', flexDirection: 'column', minHeight: '100%' }}>
                    <div style={{ flex: '1' }}>
                        <Row style={{ ...gbstyle.formpopuptitle, ...gbstyle.formpopupborderbottom }}>
                            <Col style={{ ...gbstyle.formtoppic2 }}>
                                Customer Details
                            </Col>
                            <Col md={1} style={{ display: 'flex', justifyContent: 'flex-end' }}>
                                <Button
                                    size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.buttonxsm }}
                                    onClick={() => setShowForm(false)}>
                                    <CloseOutlined className="nofixtop" />
                                </Button>
                            </Col>
                        </Row>
                        <Row style={{ ...gbstyle.formpopupbody }}>
                            <Form style={{ ...gbstyle.controlfull }}>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Full Name</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Email*</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Contact Number</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Company Name</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Address</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" as="textarea" rows="3" style={gbstyle.formtextarearound} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div style={gbstyle.textlabel}>Tax ID</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <Form.Control size="sm" type="text" style={{ ...gbstyle.formround }} />
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div class="hr-sect">Document</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col >
                                            <div className="smallscroll" style={{ overflowX: 'auto', width: '100%', display: 'flex', flexDirection: 'row', flexWrap: 'nowrap', justifyContent: 'space-between', padding: '10px 0px 10px 0px' }}>
                                                {images.map((x, i) => {
                                                    if (x != 'empty') {
                                                        return (
                                                            <Image
                                                                className="antimground"
                                                                width="110px"
                                                                src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
                                                                style={{ ...gbstyle.formnextcol, minWidth: 110 }}
                                                            />)
                                                    } else {
                                                        return (
                                                            <div
                                                                className="antimground"
                                                                style={{ minWidth: "110px", height: "110px", ...gbstyle.formnextcol, borderRadius: 15, border: '1px dashed #CACACA', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}
                                                            >
                                                                <div style={{ fontSize: '48px' }}>
                                                                    <FontAwesomeIcon icon={['fas', 'image']} style={{ ...gbstyle.formimage }} />
                                                                </div>
                                                                <div style={{ whiteSpace: 'pre-line', textAlign: 'center', position: 'relative', top: '-10px' }}>
                                                                    {'Add File\n(image , pdf)'}
                                                                </div>
                                                            </div>)
                                                    }
                                                }
                                                )}
                                            </div>
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group >
                                    <Row>
                                        <Col>
                                            <div class="hr-sect">Take care By</div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col >
                                            <Select
                                                showSearch
                                                allowClear
                                                optionFilterProp="children"
                                                filterOption={(input, option) => (
                                                    option.props.children.toLowerCase()
                                                        .indexOf(input.toLowerCase()) >= 0
                                                )}
                                                style={{ ...gbstyle.formcontrol, width: '100%' }} className="dropdownround">
                                                <Option value="jack">Jack</Option>
                                                <Option value="lucy">Lucy</Option>
                                                <Option value="Yiminghe">yiminghe</Option>
                                            </Select>
                                        </Col>
                                    </Row>
                                </Form.Group>
                            </Form>
                        </Row>
                    </div>

                    <div style={{ flex: 'none' }}>
                        <Row style={{ ...gbstyle.formpopupfooter, alignItems: 'center', width: '100%' }}>
                            <div class="hr-sect" style={{ ...gbstyle.textweight3 }}>Update History</div>
                            <div>

                                <div style={{ display: 'block', clear: 'both', float: 'left' }}>Last Updated: </div>
                                <div style={{ display: 'block', clear: 'both', float: 'left' }}>By: </div>
                            </div>
                        </Row>
                        <Row style={{ ...gbstyle.formpopupfooter, ...gbstyle.formpopupbordertop, alignItems: 'center', justifyContent: 'space-between', paddingRight: '30px' }}>
                            <div>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>ลบ Account</Button>
                            </div>
                            <div>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbuttonoutline, ...gbstyle.formnextcol }}>Reset Password</Button>
                                <Button size="sm" style={{ ...gbstyle.formround, ...gbstyle.formbutton, ...gbstyle.formnextcol }}>Save</Button>
                            </div>
                        </Row>
                    </div>
                </div>
            </Drawer>
        </div >
    )
}


